(function($) {
  $.fn.zakazCounter = function() { //yamCounter

    var T, I, A, S;

    function Recalc(selector, value) {
      // console.log(selector, value);

      $(selector).each(function() {
        if($(this).attr("data-value")) {
          var v = $(this).attr("data-value");
          if(v.search(".") != -1) {
            v *= value;
            v = v.toFixed(2);
            v = new String(v);
            v = v.replace(/\./g, ",");
          }
          else {
            v = parseInt(v);
            v *= value;
          }

          $(this).text(v);
        }
        else {
          var v = $(this).text();
          if(v.search(",") != -1 || v.search(".") != -1) {
            v = v.replace(",", ".");
            v = parseFloat(v);
            $(this).attr("data-value", v);
            v *= value;
            v = v.toFixed(2);
            v = new String(v);
            v = v.replace(/\./g, ",");
          }
          else {
            v = parseInt(v);
            v *= value;
          }

          $(this).text(v);
        }

      });
    }

    $(document).on("keydown", "[data-counter='field']", function(e) {
      if(e.keyCode == 13) {
        e.preventDefault();
      }
    })

    $(document).on("keyup", "[data-counter='field']", function(e) {
      e.preventDefault();
      clearTimeout(S);
      var num = 0, min, max, step;

      if($(this).closest('[data-zakaz-counter]').data('zakaz-counter') != '') {//[data-yam-counter]
        var arr = $(this).closest('[data-zakaz-counter]').data('zakaz-counter');//[data-yam-counter]').data('yam-counter')
        min = arr[0];
        max = arr[1];
        step = arr[2];
      }
      else {
        min = 1;
        max = 9999;
        step = 1;
      }

      if(e.keyCode == 13) {
        var s = parseFloat($(this).val());

        if(isNaN(s) || s == null || s == "") s = min;
        if(s > max) s = max;
        $(this).val(s);

        var id = $(this).closest("[data-zakaz-counter]").attr("data-trigger-refresh");

        if(id) {
          if($("#" + id).length) {
            $(this).bind("change", function() {
              S = setTimeout(function() {
                $("#" + id).trigger("click");
              }, 1500);
            });
          }
          else {
            console.info("Кнопка #"+ id +", по якій емулюється клік, не знайдена");
          }
        }

        $(this).trigger("change");
        $(this).unbind("change");
        $(this).blur();
      }
    });

    $(document).on("mousedown", "[data-counter='plus'], [data-counter='minus']", function(e) {
      e.preventDefault();
      var field = $(this).closest('[data-zakaz-counter]').find('[data-counter="field"]');
      var num = 0, min, max, step, float;
      var recalc = $(this).closest("[data-zakaz-counter]").attr("data-recalc");
      var summ = $($(this).closest("[data-zakaz-counter]").attr("data-summ"));
      var price = parseInt($("[itemprop='price']").text());

      if($(this).data('zakaz-counter') != '') {
        var arr = $(this).closest('[data-zakaz-counter]').data('zakaz-counter');
        min = arr[0];
        max = arr[1];
        step = arr[2];
        float = arr[3];
      }
      else {
        min = 1;
        max = 9999;
        step = 1;
      }

      if($(this).data('counter') == 'plus') {
        T = setTimeout(function() {
          I = setInterval(function() {

            if(float) {
              num = parseFloat(field.val());
            }
            else {
              num = parseInt(field.val());
            }

            if(num == '' || num == null || isNaN(num)) num = 0;
            num += step;
            if(num > max) num = max;

            if(float) {
              num = Math.round(num * Math.pow(10, float)) / Math.pow(10, float);
              num = num.toFixed(float);
            }
            else {
              num = parseInt(num);
            }

            field.val(num);
            summ.text((num * price).toFixed(float))
            Recalc(recalc, num);
          }, 100);
        }, 500);
      }
      if($(this).data('counter') == 'minus') {
        T = setTimeout(function() {
          I = setInterval(function() {
            if(float) {
              num = parseFloat(field.val());
            }
            else {
              num = parseInt(field.val());
            }

            if(num == '' || num == null || isNaN(num)) num = 0;
            num -= step;
            if(num < min) num = min;

            if(float) {
              num = Math.round(num * Math.pow(10, float)) / Math.pow(10, float);
              num = num.toFixed(float);
            }
            else {
              num = parseInt(num);
            }
            field.val(num);
            summ.text((num * price).toFixed(float));
            Recalc(recalc, num);
          }, 100);
        }, 500);
      }
    });

    $(document).on("mouseup", "[data-counter='plus'], [data-counter='minus']", function(e) {
      e.preventDefault();
      clearTimeout(T);
      clearTimeout(A);
      clearTimeout(S);
      clearInterval(I);
      var field = $(this).closest('[data-zakaz-counter]').find('[data-counter="field"]');
      var recalc = $(this).closest("[data-zakaz-counter]").attr("data-recalc");
      var summ = $($(this).closest("[data-zakaz-counter]").attr("data-summ"));
      var price = parseInt($("[itemprop='price']").text());
      var num = 0, min, max, step, float;

      if($(this).data('zakaz-counter') != '') {
        var arr = $(this).closest('[data-zakaz-counter]').data('zakaz-counter');
        min = arr[0];
        max = arr[1];
        step = arr[2];
        float = arr[3];
      }
      else {
        min = 1;
        max = 9999;
        step = 1;
      }

      if($(this).data('counter') == 'plus') {
        if(float) {
          num = parseFloat(field.val());
        }
        else {
          num = parseInt(field.val());
        }
        if(num == '' || num == null || isNaN(num)) num = 0;
        num += step;
        if(num > max) num = max;

        if(float) {
          num = Math.round(num * Math.pow(10, float)) / Math.pow(10, float);
          num = num.toFixed(float);
        }
        else {
          num = parseInt(num);
        }
        field.val(num);
        summ.text((num * price).toFixed(float));
        Recalc(recalc, num);
      }
      if($(this).data('counter') == 'minus') {
        if(float) {
          num = parseFloat(field.val());
        }
        else {
          num = parseInt(field.val());
        }
        if(num == '' || num == null || isNaN(num)) num = 0;
        num -= step;
        if(num < min) num = min;

        if(float) {
          num = Math.round(num * Math.pow(10, float)) / Math.pow(10, float);
          num = num.toFixed(float);
        }
        else {
          num = parseInt(num);
        }
        field.val(num);
        summ.text((num * price).toFixed(float));
        Recalc(recalc, num);
      }

      var id = $(this).closest("[data-zakaz-counter]").attr("data-trigger-refresh");

      if(id) {
        if($("#" + id).length) {
          field.bind("change", function() {
            S = setTimeout(function() {
              $("#" + id).trigger("click");
            }, 1500);
          });
        }
        else {
          console.info("Кнопка #"+ id +", по якій емулюється клік, не знайдена");
        }
      }

      field.trigger("change");
      field.unbind("change");
    });

    $("#page-zakaz [data-counter='plus']").trigger("mouseup");
    $("#page-zakaz [data-counter='minus']").trigger("mouseup");
  };
})($);
